@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fraunces:wght@900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.testimonials {
	background: rgb(255, 255, 255);
  padding-bottom: 150px;
}

.h2-client-testimonials {
	text-transform: uppercase;
	font-size: 25px;
	text-align: center;
	padding-top: 100px;
	font-family: 'Fraunces';
  color: rgb(0, 0, 0, .5
  );
  letter-spacing: 4px;
  margin-bottom: 65px;
}

.clients {
  display: flex;
  padding-left: 3%;
  padding-right: 3%;
}

.client-photo {
	border-radius: 50%;
  width: 15%;
}

.client-testemonial {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.client-photo {
  margin-bottom: 35px;
}

.p-testemonial {
  margin-bottom: 35px;
  line-height: 30px;
  color: rgb(0, 0, 0, .5);
}

.p-name-client {
  font-weight: 900;
  font-family: 'Fraunces';
  margin-bottom: 10px;
}

.p-function-client {
  font-family: 'Barlow';
  font-size: 12px;
  color: rgb(0, 0, 0, .5)
}


.row-bottom {
  display: flex;
  
}

.row-bottom > img {
  width: 25%;
}
* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.container {
  padding: 0 3%;
}

ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul li {
  margin: 0 15px;
}

ul li a {
  color:rgb(255, 255, 255)
}

.title-sunnyside {
  color: rgb(255, 255, 255);
}

.App {
  background: rgb(63, 191, 254);
}

header {
  display: flex;
  flex-direction: column;
  background-image: url(../../static/media/image-header.49b52ffa.jpg);
  height: 600px;
  background-size: 100% 600px;
  box-sizing: cover;
}

header > img {
  width: 100%;
  align-self: center;
  /* -webkit-transform: scale(1.5); */
}

header p {
  text-transform: uppercase;
  color:rgb(255, 255, 255);
  font-family: 'Fraunces';
  font-weight: 900;
  font-size: 35px;
  text-align: center;
  z-index: 1;
}

.title-navigation {
  margin-bottom: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 30px;
}

.btn-contact {
  color: rgb(0, 0, 0);
  background: rgb(255, 255, 255);
  border: none;
  border-radius: 25px;
  height: 35px;
  text-align: center;
  width: 100px;
  cursor: pointer;
}

.row-first, .row-second, .row-third {
  height: 450px;
  display: flex;
}

h2 {
  font-family: 'Fraunces';
  font-weight: 600;
}

.text-learn-more {
  margin-top: 30px;
  text-transform: uppercase;
  font-family: 'Barlow';
  font-weight: 900;
}

.text-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.img-egg {
  width: 50%;
}

.text-card h2 {
  margin-bottom: 15px;
}

.row-first > div, .row-second > div{
  width: 50%;
  background: rgb(255, 255, 255);
}

.text {
  width: 50%;
  margin: 0 auto;
}

.row-third div {
  width: 50%;
  box-sizing: content-box;
}

.graphic-design {
  background-size: cover;
  background-image: url(../../static/media/image-graphic-design.7ac8bdf0.jpg);
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 60px;
}

.text-graphic-design {
  text-align: center;
}

.text-graphic-design h2 {
  margin-bottom: 20px;
}

.photography {
  background-size: cover;
  background-image: url(../../static/media/image-photography.104cf40b.jpg);
}

.photography {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 60px;
  text-align: center;
}

.text-photography h2 {
  margin-bottom: 20px;
}

footer {
  height: 300px;
  background: rgb(167, 211, 200);
  color: rgb(44, 117, 102);
  font-family:'Fraunces', sans-serif ;
  display: flex;
  flex-direction: column;
  align-items: center;
}

footer h2 {
  font-size: 35px;
  font-family: 'Barlow';
  font-weight: black;
  margin-top: 80px;
  margin-bottom: 30px;
}

footer nav {
  margin-bottom: 50px;
}

footer nav ul li {
  margin-right: 40px;
}

.slider {
  position: relative;
  top: 50px;
  width: 30px;
  -webkit-animation: slider 1s;
          animation: slider 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

